import React from "react";
import { Parallax, Background } from "react-parallax";
import "../pages/styles/Parallax.css";

class Paralax extends React.Component {
  render() {
    return (
      <div>
        <Parallax
          bgImage="https://cdn.wallpapersafari.com/99/70/pbsUl7.jpg"
          style={{
            color: "white",
            padding: "15px",
            display: "flex",
            justifyContent: "center",
            alignItems: "top",
            webkitAlignItems: "top",
            height: "1080px"
          }}
          strength={300}
        >
          <div
            style={{
              color: "white",
              fontSize: "50px",
              padding: "20px",
              width: "250px",
              backgroundColor: "black",
              textAlign: "center"
            }}
          >
            <div>Cosmo</div>
          </div>

          <div style={{ height: "1080px" }} />
        </Parallax>

        <Parallax
          blur={{ min: -15, max: 15 }}
          bgImage="http://antropocene.it/wp-content/uploads/2017/05/Democrazia-Energocratica.jpg"
          bgImageAlt="the cat"
          style={{
            color: "white",
            padding: "15px",
            display: "flex",
            justifyContent: "center",
            alignItems: "center"
          }}
          strength={300}
        >
          <div
            style={{
              color: "white",
              fontSize: "50px",
              padding: "20px",
              width: "250px",
              backgroundColor: "black",
              textAlign: "center"
            }}
          >
            <div>Nature</div>
          </div>

          <div style={{ height: "1080px" }} />
        </Parallax>

        <Parallax
          blur={0}
          bgImage="http://doanarae.com/doanarae/10065-3d-hd-wallpaper-widescreen_42262.jpg"
          bgImageAlt="the cat"
          style={{
            color: "white",
            padding: "15px",
            display: "flex",
            justifyContent: "center",
            alignItems: "center"
          }}
          strength={300}
        >
          <div
            style={{
              color: "white",
              fontSize: "50px",
              padding: "20px",
              width: "250px",
              backgroundColor: "black",
              textAlign: "center"
            }}
          >
            <div>Dreaming</div>
          </div>

          <div style={{ height: "1080px" }} />
        </Parallax>
      </div>
    );
  }
}

export default Paralax;
